import React from "react";
import { Link } from "react-router-dom";

import './Header.css';
import logoutIcon from '../../images/logout.svg'

export default function Header(){
    return(
        <div>
        <div className='header'>
            <h1 className='header__title'>Simple Hotel Check</h1>
            <Link to='/' className='header__logout-button'>Выйти<img className='header__logout-image' src={logoutIcon} /></Link>
        </div>
        <main className='main'>
            <div className="search">
                <label htmlFor='location' className='search__label'>Локация</label>
                <input id='location' className='search__input' type='text' />
                <label htmlFor='checkin' className='search__label'>Дата заселения</label>
                <input id='checkin' type='date' className='search__input' />
                <label htmlFor='daysamount' className='search__label'>Количество дней</label>
                <input id='daysamount' type='number' className='search__input' />
                <button type='submit' className='button'>Найти</button>
            </div>
            <div className='result'>
                <h2 className='result__title'>Отели</h2>
                <p className='result__arrow'>&#10095;<span className='result__location'>ss</span></p>
            </div>
            <div className='featured'>
                <h2 className='featured__title'>Избранное</h2>
            </div>
        </main>
        </div>
    )
}