import React from "react";
import './Login.css';

export default function Login(){
    return(
        <div className='login'>
            <form className='form'>
                <h1 className='login__title'>Simple Hotel Check</h1>
                <label className='form__input-name' type='text' htmlFor='login'>Логин</label>
                <input className='form__input-data' id='login'/>
                <label className='form__input-data' className='form__input-name' htmlFor='password'>Пароль</label>
                <input className='form__input-data' type='password' id='password'/>
                <button className='button' type='submit'>Войти</button>
            </form>
        </div>
    )
}