class Api{
    constructor(data){
        this._baseUrl = data.baseUrl
    }

    _checkResponse(res){
        if (res.ok) {
          return res.json()
        }
        else{return Promise.reject(`Ошибка: ${res.status}`)}
    }  

    getInitialHotels(){
        return fetch(`http://engine.hotellook.com/api/v2/cache.json?location=Moscow&currency=rub&checkIn=2021-12-25&checkOut=2021-12-26&limit=10`, {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json'
            }
          })
          .then(this._checkResponse)
        
    }
}

const api = new Api({
    baseUrl: 'http://engine.hotellook.com/api/v2/cache.json'
});

export default api;